import { useCallback, useEffect, useState } from "react";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  assestsNavbarUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import {
  useAdmissionOngoingApplication,
  useAdmissionOngoingDirectApplicationExcel,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import OngoingAdmissionCard from "./OngoingAdmissionCard";
import CreateApplicationConfig from "./CreateApplicationConfig";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import CreateButton from "../../../Ui/Button/CreateButton";
import UploadExcelButton from "../../../Ui/Button/UploadExcelButton";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";

const OngoingAdmission = ({ aid, instituteId, fid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);
  const [fileUpload] = useFileUpload();
  const [disabled, setDisabled] = useState(false);

  const [admissionOngoingDirectApplicationExcel] =
    useAdmissionOngoingDirectApplicationExcel();
  const {
    admissionOngoingApplication,
    admissionOngoingApplicationLoading,
    admissionOngoingApplicationRefetch,
  } = useAdmissionOngoingApplication({
    data: {
      aid: aid,
      page: page,
      limit: 1000,
      search: search,
    },
    skip: !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplication?.list?.length) {
      let obj = nextPage(admissionOngoingApplication?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplication?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    admissionOngoingApplicationRefetch();
  };

  const onNavigate = useCallback(() => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/admission`
        : `/q/${params.username}/member/staff/admission`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  const onDirectApplicationExcel = (e) => {
    if (e.target?.files?.length && aid) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            admissionOngoingDirectApplicationExcel({
              aid: aid,
              bodyParameter: {
                excel_file: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  // console.info("admissionOngoingApplication", getQuery);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.5rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{t("ongoing_applications")}</h6>
            </div>

            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    style={{
                      minWidth: "220px",
                    }}
                  />
                </div>
              </section>
              {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : (
                <>
                  <UploadExcelButton
                    onExcelFileUpload={onDirectApplicationExcel}
                    dynamicKey="direct_application"
                    title="Direct Application Excel"
                    label="Direct Application Excel"
                  />
                  <CreateButton
                    label="create_new_application"
                    customStyle={{
                      paddingBlock: "0.4rem",
                      margin: "0",
                    }}
                    onAction={onToggle}
                  />
                </>
              )}
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
            position: "relative",
          }}
        >
          {getQuery.state?.authorizedApplication?.length > 0
            ? admissionOngoingApplication?.list?.map((application) =>
                getQuery.state?.authorizedApplication?.includes(
                  application?._id
                ) ? (
                  <OngoingAdmissionCard
                    application={application}
                    key={application?._id}
                    aid={aid}
                    instituteId={instituteId}
                    onRefetch={onRefetch}
                  />
                ) : null
              )
            : admissionOngoingApplication?.list?.map((application) => (
                <OngoingAdmissionCard
                  application={application}
                  key={application?._id}
                  aid={aid}
                  instituteId={instituteId}
                  onRefetch={onRefetch}
                />
              ))}

          {/* admissionOngoingApplication?.list?.map((application, index) =>
                admissionOngoingApplication?.list?.length === index + 1 ? (
                  <div key={application?._id} ref={ref}>
                    <OngoingAdmissionCard
                      application={application}
                      aid={aid}
                      instituteId={instituteId}
                      onRefetch={onRefetch}
                    />
                  </div>
                ) : (
                  <OngoingAdmissionCard
                    application={application}
                    key={application?._id}
                    aid={aid}
                    instituteId={instituteId}
                    onRefetch={onRefetch}
                  />
                )
              ) */}
          {admissionOngoingApplicationLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            admissionOngoingApplication?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No ongoing application found related this search."
              />
            )
          ) : (
            admissionOngoingApplication?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No application created."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <CreateApplicationConfig
          onClose={onToggle}
          onRefetch={onRefetch}
          aid={aid}
          instituteId={instituteId}
          fid={fid}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default OngoingAdmission;
